import { IRequestParams, IResultContent, request } from "@fanfanlo";
import { tms } from "../tms";
import { IRequestOptions } from "../IRequestOptions";

/**
 * @description 
 */
export interface IMeetingUserInfo {
    user_id: string;
    display_name: string;
    avatar_url?: string;
}

/**
 * @description 
 */
export interface IAvailableMeeting {
    event_uuid: string;
    short_code: string;
    role: 'initiator' | 'responder';
    status: string;
    created_at: number;
    initiator_info: IMeetingUserInfo;
    livekit_room_name: string;
    access_mode: string;
}

/**
 * @description 
 */
export interface IEventCallListAvailableMeetingsParams extends IRequestParams {
    // API
}

/**
 * @description 
 */
export interface IEventCallListAvailableMeetingsResult extends IResultContent {
    data: {
        is_ok: boolean;
        msg: string;
        meetings: IAvailableMeeting[];
    };
}

/**
 * @description 
 * @param opts  params, gateway, handler 
 */
export async function tmsEventCallListAvailableMeetings(opts: IRequestOptions<IEventCallListAvailableMeetingsParams>) {
    const res = await request<IEventCallListAvailableMeetingsResult>(
        tms.TEventCall, 
        tms.MEventCallListAvailableMeetings, 
        opts.params, 
        opts.gateway, 
        opts.handler
    );
    return res;
}