export type AvailableMeeting = IAvailableMeeting;
export type MeetingUserInfo = IMeetingUserInfo;

/**
 * 
 */
export async function getAvailableMeetings(): Promise<AvailableMeeting[]> {
  try {
    const response = await tmsEventCallListAvailableMeetings({
      params: {}, // 
      handler: { printLog: true }
    });

    console.log(' Meeting API response:', response);

    // 
    if (response.err || !response.shellIsOk || !response.dataIsOk) {
      const errorMessage = response.err?.message || 'Failed to fetch available meetings';
      throw new Error(errorMessage);
    }

    if(!response.data?.data) {

    }
    // 
    if (!response.data?.is_ok) {
      throw new Error(response.data?.data.msg || 'Business logic error');
    }

    return response.data.meetings;
  } catch (error) {
    console.error(' Error fetching available meetings:', error);
    throw error;
  }
}

/**
 * shortcodeURL
 */
export function buildMeetingUrl(shortCode: string, role: 'initiator' | 'responder'): string {
  const baseUrl = '/join-call';
  return `${baseUrl}?code=${shortCode}&role=${role}`;
}

/**
 * 
 */
export function formatMeetingTime(timestamp: number): string {
  const date = new Date(timestamp * 1000);
  const now = new Date();
  const diffMs = now.getTime() - date.getTime();
  const diffMins = Math.floor(diffMs / (1000 * 60));
  const diffHours = Math.floor(diffMins / 60);

  if (diffMins < 1) {
    return '';
  } else if (diffMins < 60) {
    return `${diffMins}`;
  } else if (diffHours < 24) {
    return `${diffHours}`;
  } else {
    return date.toLocaleDateString('zh-CN', {
      month: 'short',
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    });
  }
}

/**
 * 
 */
export function getRoleDisplayText(role: 'initiator' | 'responder'): string {
  return role === 'initiator' ? '' : '';
}

/**
 * 
 */
export function getMeetingStatusInfo(status: string): { text: string; color: string } {
  switch (status.toLowerCase()) {
    case 'active':
    case 'in_progress':
      return { text: '', color: '#4caf50' };
    case 'waiting':
      return { text: '', color: '#ff9800' };
    case 'ended':
      return { text: '', color: '#757575' };
    default:
      return { text: status, color: '#757575' };
  }
}